<script>
import axios from 'axios';

export default {
    data() {
        return {
            teacherData: [],
            teacherID: '',

        }
    },
    mounted() {
        const storedID = sessionStorage.getItem('teacherID');
        this.teacherID=storedID;
        this.getByID();

    },
    methods: {
        getAll() {
            axios.get("/api/teacher").then((result) => {
                this.teacherData=result.data;
            });
        },
        getByID(){
            axios.get(`/api/teacher/id/${this.teacherID}`).then((result) => {
                this.teacherData=result.data.data;
            });

        },
        
    },
}
</script>

